  z-index: 0;
  pointer-events: none;
  width: 774px;
  position: absolute;
  top: 0;
  right: 0;
  flex-shrink: 0;
}

@media only screen and (max-width: 1599.9px) {
  .contacts__box {
    width: 619px;
  }
}

@media only screen and (max-width: 1279.9px) {
  .contacts__box {
    width: 100%;
    margin-bottom: 36px;
    position: static;
    height: 0;
  }
}

.contacts__box_active {
  opacity: 1;
  z-index: 1;
  pointer-events: all;
}

@media only screen and (max-width: 1279.9px) {
  .contacts__box_active {
    height: auto;
  }
}

.contacts__map {
  margin-bottom: 14px;
  height: 609px;
  width: 774px;
  border-radius: 32px;
  overflow: hidden;
}

@media only screen and (max-width: 1599.9px) {
  .contacts__map {
    margin-bottom: 11px;
    height: 487px;
    width: 619px;
    border-radius: 26px;
  }
}

@media only screen and (max-width: 1279.9px) {
  .contacts__map {
    margin-bottom: 18px;
    margin-left: -18px;
    margin-right: -18px;
    height: 283px;
    width: auto;
    border-radius: 0;
  }
}

.contacts__gallery {
  display: flex;
}

.contacts__gallery-picture {
  width: 185px;
}

.contacts__gallery-picture:not(:last-of-type) {
  margin-right: 14px;
}

@media only screen and (max-width: 1599.9px) {
  .contacts__gallery-picture:not(:last-of-type) {
    margin-right: 12px;
  }
}

@media only screen and (max-width: 1279.9px) {
  .contacts__gallery-picture:not(:last-of-type) {
    margin-right: 7px;
  }
}

@media only screen and (max-width: 1599.9px) {
  .contacts__gallery-picture {
    width: 147px;
  }
}

@media only screen and (max-width: 1279.9px) {
  .contacts__gallery-picture {
    width: 103px;
  }
}

.contacts__gallery-image {
  height: 123px;
  border-radius: 12px;
  object-fit: cover;
}

@media only screen and (max-width: 1599.9px) {
  .contacts__gallery-image {
    height: 98px;
    border-radius: 9px;
  }
}

@media only screen and (max-width: 1279.9px) {
  .contacts__gallery-image {
    height: 68px;
    border-radius: 7px;
  }
}

.modal-registration {
  display: none;
}

.modal-registration.fancybox__content {
  border-radius: 32px;
  padding: 28px 36px 33px;
  color: #fff;
  width: 498px;
  background-image: url("../img/modal/modal_bg.jpg");
  background-size: cover;
}

@media only screen and (max-width: 767px) {
  .modal-registration.fancybox__content {
    padding: 18px 18px 25px;
    max-width: 360px;
    width: 100%;
    border-radius: 22px;
    background-image: url("../img/modal/modal_bg-mobile.jpg");
  }
}

.modal-registration.fancybox__content .f-button.is-close-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #fff;
  opacity: 1;
  top: -35px;
  right: -42px;
}

@media only screen and (max-width: 767px) {
  .modal-registration.fancybox__content .f-button.is-close-btn {
    width: 30px;
    height: 30px;
    top: 15px;
    right: 15px;
  }
}

.modal-registration.fancybox__content .f-button.is-close-btn svg {
  stroke: #111519;
  stroke-width: 3px;
  width: 20px;
  height: 20px;
}

@media only screen and (max-width: 767px) {
  .modal-registration.fancybox__content .f-button.is-close-btn svg {
    width: 12px;
    height: 12px;
  }
}

.modal-registration__title {
  margin-bottom: 12px;
  font-size: 30px;
  font-weight: 600;
  line-height: 130%;
  text-transform: uppercase;
}

@media only screen and (max-width: 767px) {
  .modal-registration__title {
    margin-bottom: 15px;
    font-size: 22px;
    line-height: 120%;
  }
}

.modal-registration__text {
  margin-bottom: 25px;
  max-width: 325px;
  font-size: 14px;
  line-height: 160%;
}

@media only screen and (max-width: 767px) {
  .modal-registration__text {
    max-width: 300px;
    margin-bottom: 16px;
    font-size: 13px;
  }
}

.modal-registration__label {
  position: relative;
  display: block;
  width: 100%;
}

.modal-registration__label:not(:last-of-type) {
  margin-bottom: 26px;
}

@media only screen and (max-width: 767px) {
  .modal-registration__label:not(:last-of-type) {
    margin-bottom: 14px;
  }
}

.modal-registration__input {
  width: 100%;
  padding: 23px 0 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  font-size: 18px;
  line-height: 160%;
  letter-spacing: 0.18px;
  color: #fff;
  background-color: transparent;
}

@media only screen and (max-width: 767px) {
  .modal-registration__input {
    font-size: 15px;
    letter-spacing: 0.15px;
    padding: 20px 0;
  }
}

.modal-registration__input:placeholder-shown + .modal-registration__placeholder {
  top: 23px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 18px;
  letter-spacing: 0.18px;
}

@media only screen and (max-width: 767px) {
  .modal-registration__input:placeholder-shown + .modal-registration__placeholder {
    font-size: 15px;
    letter-spacing: 0.15px;
  }
}

.modal-registration__input:focus + .modal-registration__placeholder {
  position: absolute;
  top: 4px;
  left: 0;
  color: #fff;
  font-size: 11px;
  font-weight: 500;
  line-height: 160%;
  letter-spacing: 0.11px;
}

@media only screen and (max-width: 767px) {
  .modal-registration__input:focus + .modal-registration__placeholder {
    top: 0;
    font-size: 10px;
    letter-spacing: 0.1px;
  }
}

.modal-registration__placeholder {
  position: absolute;
  top: 4px;
  left: 0;
  color: #fff;
  font-size: 11px;
  font-weight: 500;
  line-height: 160%;
  letter-spacing: 0.11px;
  transition-duration: 0.3s;
  transition-property: top, font-size, color, letter-spacing;
  color: rgba(255, 255, 255, 0.6);
}

@media only screen and (max-width: 767px) {
  .modal-registration__placeholder {
    font-size: 10px;
    letter-spacing: 0.1px;
    top: 0;
  }
}

.modal-registration__submit {
  margin-top: 36px;
  margin-bottom: 138px;
  width: 100%;
  font-size: 20px;
  padding: 26px 30px 30px;
}

@media only screen and (max-width: 767px) {
  .modal-registration__submit {
    margin-top: 30px;
    margin-bottom: 142px;
    font-size: 18px;
    padding: 16px 16px 17px;
  }
}

.modal-registration__policy {
  max-width: 364px;
  margin: 0 auto;
  color: rgba(255, 255, 255, 0.6);
  text-align: center;
  font-size: 12px;
  line-height: 160%;
}

@media only screen and (max-width: 767px) {
  .modal-registration__policy {
    max-width: 290px;
    text-align: left;
    margin: 0;
    font-size: 10px;
  }
}

.modal-registration__policy a {
  color: #fff;
  text-decoration: none;
}

.modal-registration__policy a:hover {
  text-decoration: underline;
}

.modal-video.fancybox__content {
  padding: 0;
  width: 80%;
  height: 80%;
  max-width: calc(100% - 100px);
  max-height: calc(100% - 88px);
  overflow: visible;
  background: #fff;
}

.modal-video {
  display: none;
}

.modal-video__video {
  background: #000;
  width: 100%;
  height: 100%;
}

